<template>
  <div>
    <van-sticky>
      <van-nav-bar title="添加" left-arrow @click-left="onClickLeft" />
    </van-sticky>
    
    <div class="userWrap">
    <van-field label="昵称" placeholder="请输入昵称" v-model="userInfo.name"  />
    <van-field label="钱包地址" placeholder="请输入钱包地址" v-model="userInfo.money_address"  />
    <van-field label="电话" placeholder="请输入电话" v-model="userInfo.phone"  />
    <!-- <van-cell title='收款码'></van-cell> -->
    <div style="font-size:14px;margin:9px 14px;">
        收款码
    </div>
      <van-uploader :after-read="upImg" :accept="'image/*'" v-if="userInfo.money_qrcode">
          <img class="cover" :src="userInfo.money_qrcode" />
        </van-uploader>
        <van-uploader :after-read="upImg" :accept="'image/*'" v-else>
          <img class="cover" src="../../assets/imgs/personal/loadinge.png" />
        </van-uploader>
        <div style="margin-left:7%;font-size:15px;">
            上传截图
        </div>
    <!-- <van-cell-group>
    <van-field label="" placeholder="请输入对方账号" v-model="userInfo.pid_member"  />
    <van-field label="转账数量" placeholder="请输入转账数量" v-model="userInfo.pid_member"  />
      </van-cell-group> -->
      <button class="submitBtn" @click="updateUser">确定</button>
    </div> 
  </div>
</template>

<script>
import { userInfo, uploadImg, updateUser,addWallet } from "@/api/personal";
export default {
  data() {
    return {
      userInfo: {
      name:"",
      money_address:"",
      phone:"",
      money_qrcode:"",
      },
      img: ""
    };
  },
  created() {
  },
  methods: {

    onClickLeft() {
      this.$router.go(-1);
    },
    upImg(file) {
      this.image = file.content.substring(file.content.indexOf(",") + 1);
      uploadImg(this.image, "").then(resp => {
        this.userInfo.money_qrcode = resp.img;
      });
    },
    // 提交
    updateUser() {
             var myreg=/^[1][3,4,5,7,8][0-9]{9}$/;
            if (!myreg.test(this.userInfo.phone)) {
          this.$toast("手机号码格式错误!");
            return false
        }else{
                addWallet(this.userInfo).then(res => {
          if(res.errcode==0){
          this.$toast("添加成功!");
          this.userInfo={}
          }else{
          this.$toast(res.msg);

          }
       
      });
        }
  
    }
  }
};
</script>
<style lang="scss" scoped>
.van-uploader{
    position: relative;
    left: 5%;
}
.cover{
    width: 80px;
    height: 80px;
}
.content{
    margin: 15px 5%;
    width: 90%;
    height: 150px;
    box-shadow: 0 4px 5px 5px solid rgba(255,255,255,1);
    border: 1px solid lightgray;
}
.text{
    border: none;
    background: none;
    font-size: 15px;
    text-indent: 40px;
    width: 100%;
}

/deep/ .van-field__label{
    width: 120px;
}
.van-button--primary{
     background: white;
     color: #28A6FF;
     border: 1px solid #28A6FF;
     border-radius: 20px;
}
.van-cell {
  padding: 15px 16px 19px;
}
.userImg {
  width: 39px;
  height: 39px;
}
.submitBtn {
  display: block;
  width: 345px;
  height: 40px;
  line-height: 40px;
  color: #fff;
  font-size: 18px;
  border-radius: 20px;
  background: #28A6FF;
  margin: 66px auto;
  border: 0 none;
}
</style>
